"use client"

import { useState } from "react"
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Badge } from "@/components/ui/badge"
import { SearchBar } from "@/components/toolbar/SearchBar"
import RedeemConfirmDialog from "./redeemConfirm-dialog"

const mockCustomers = [
  { id: 1, name: "Amit Kumar", upiId: "amit@upi", stamps: 12, visits: 15, lastVisit: "2 days ago" },
  { id: 2, name: "Priya Singh", upiId: "priya@upi", stamps: 8, visits: 10, lastVisit: "1 day ago" },
  { id: 3, name: "Vikram Patel", upiId: "vikram@upi", stamps: 5, visits: 6, lastVisit: "3 days ago" },
  { id: 4, name: "Neha Sharma", upiId: "neha@upi", stamps: 20, visits: 25, lastVisit: "Today" },
  { id: 5, name: "Rahul Verma", upiId: "rahul@upi", stamps: 3, visits: 2, lastVisit: "4 days ago" },
  { id: 6, name: "Sita Devi", upiId: "sita@upi", stamps: 15, visits: 12, lastVisit: "1 week ago" },
]

export default function CustomersList() {
  const [search, setSearch] = useState("")
  const [currentPage, setCurrentPage] = useState(1)
  const itemsPerPage = 5

  // FILTER
  const filtered = mockCustomers.filter(
    (c) =>
      c.name.toLowerCase().includes(search.toLowerCase()) ||
      c.upiId.toLowerCase().includes(search.toLowerCase())
  )

  // PAGINATION LOGIC
  const totalItems = filtered.length
  const totalPages = Math.ceil(totalItems / itemsPerPage)

  const startIndex = (currentPage - 1) * itemsPerPage
  const currentItems = filtered.slice(startIndex, startIndex + itemsPerPage)

  const goToPage = (pageNum) => {
    if (pageNum >= 1 && pageNum <= totalPages) {
      setCurrentPage(pageNum)
    }
  }

  return (
    <div className="space-y-2">
      <div className="flex justify-between">
        <SearchBar
          value={search}
          onChange={(value) => {
            setSearch(value)
            setCurrentPage(1) // reset to page 1 on search
          }}
          placeholder="Search by name or phone..."
        />
      </div>

      {/* Table */}
      <Card className="overflow-hidden card">
        <Table>
          <TableHeader className="bg-muted/40 sticky top-0 z-10">
            <TableRow>
              <TableHead className="text-primary">Customer</TableHead>
              <TableHead className="text-primary">Mobile</TableHead>
              <TableHead className="text-center text-primary">Stamps</TableHead>
              <TableHead className="text-center text-primary">Visits</TableHead>
              <TableHead className="text-primary">Last Visits</TableHead>
              <TableHead className="text-primary">Reward</TableHead>
              <TableHead className="w-10" />
            </TableRow>
          </TableHeader>

          <TableBody>
            {currentItems.map((c) => (
              <TableRow key={c.id} className="hover:bg-muted/30 transition-colors">

                {/* Customer */}
                <TableCell className="py-3">
                  <div className="flex items-center gap-3">
                    <div className="h-8 w-8 rounded-full bg-primary/10 text-primary flex items-center justify-center text-xs font-semibold">
                      {c.name[0]}
                    </div>
                    <span className="font-medium">{c.name}</span>
                  </div>
                </TableCell>

                {/* UPI */}
                <TableCell className="text-muted-foreground py-3">{c.upiId}</TableCell>

                {/* Stamps */}
                <TableCell className="text-center py-3">
                  <Badge variant="default" className="rounded-full px-2 py-0.5">{c.stamps}</Badge>
                </TableCell>

                {/* Visits */}
                <TableCell className="text-center py-3">{c.visits}</TableCell>

                {/* Last Visit */}
                <TableCell className="text-muted-foreground py-3">{c.lastVisit}</TableCell>

                {/* Reward Button */}
                <TableCell className="py-3 flex gap-1">
                  {c.stamps >= 10 ? (
                    <RedeemConfirmDialog />
                  ) : (
                    <Badge variant="default" className="bg-gray-200 text-dark-text py-1.5">
                      Not Eligible
                    </Badge>
                  )}
                </TableCell>

                {/* Menu */}
                <TableCell className="text-right py-3">
                  <Button variant="ghost" size="icon" className="h-8 w-8">
                    <MoreHorizontal size={16} />
                  </Button>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>

        {/* Empty State */}
        {filtered.length === 0 && (
          <div className="py-6 text-center text-sm text-muted-foreground">
            No customers found
          </div>
        )}
      </Card>

      {/* PAGINATION UI */}
      <div className="flex justify-between items-center pt-2">
        <div className="text-sm text-muted-foreground">
          Showing {startIndex + 1}â€“{Math.min(startIndex + itemsPerPage, totalItems)} of {totalItems}
        </div>

        <div className="flex items-center gap-2">
          <Button
            variant="outline"
            size="sm"
            disabled={currentPage === 1}
            onClick={() => goToPage(currentPage - 1)}
          >
            Prev
          </Button>

          {[...Array(totalPages)].map((_, i) => (
            <Button
              key={i}
              size="sm"
              variant={currentPage === i + 1 ? "default" : "outline"}
              onClick={() => goToPage(i + 1)}
            >
              {i + 1}
            </Button>
          ))}

          <Button
            variant="outline"
            size="sm"
            disabled={currentPage === totalPages}
            onClick={() => goToPage(currentPage + 1)}
          >
            Next
          </Button>
        </div>
      </div>
    </div>
  )
}
